<template>
  <el-container id="logim-fa">
    <el-header id="login-headers"></el-header>
    <el-main id="login-main">
      <el-row type="flex" class="main-row" justify="center">
        <el-col :span="12">
          <div class="grid-content main-left">
              <img v-if="leftImage !== ''" :src="leftImage"  width="503" height="476">
              <img v-if="leftImage === ''" src="@/assets/images/login/login-bg4.png"  width="503" height="476">
          </div>
        </el-col>
        <el-col :span="12">
          <div class="grid-content main-right">
            <form class="madin-form" action="">
              <div class="form-show">
                <div class="form-top">
                  <div class="form-top-one">
                    商户管理系统
                    <p class="form-top-p">Service provider management system</p>
                  </div>
                </div>
                <div class="form-bottom">

                  <el-row class="form-bottom-row">
                    <el-col :span="3"><div class="grid-bottom"></div></el-col>
                    <el-col :span="18">
                      <div class="grid-bottom grid-border grid-update" v-bind:class="{ border2db7f5: borderColor }">
                        <span>用户名</span>
                        <input class="user-input" type="text" placeholder="" maxlength="28" v-model.trim="loginForm.username" v-on:click="updateColor('user')">
                        <i class="el-icon-user-solid form-icon" v-bind:class="{ color2db7f5: borderColor }"></i>
                        <i class="horn hornTF"></i>
                        <i class="horn hornTR"></i>
                        <i class="horn hornBF"></i>
                        <i class="horn hornBR"></i>
                      </div>
                    </el-col>
                    <el-col :span="3"><div class="grid-bottom"></div></el-col>
                  </el-row>

                  <el-row class="form-bottom-row">
                    <el-col :span="3"><div class="grid-bottom"></div></el-col>
                    <el-col :span="18">
                      <div class="grid-bottom grid-border grid-update" v-bind:class="{ border2db7f5: fontColor }">
                        <span>密&nbsp;&nbsp;&nbsp;码</span>
                        <input class="password-input" type="password" placeholder="" maxlength="18" v-model.trim="loginForm.password"  @keyup.enter="handleLogin" v-on:click="updateColor('pwd')">
                        <i class="el-icon-lock form-icon" v-bind:class="{ color2db7f5: fontColor }"></i>
                        <i class="horn hornTF"></i>
                        <i class="horn hornTR"></i>
                        <i class="horn hornBF"></i>
                        <i class="horn hornBR"></i>
                      </div>
                    </el-col>
                    <el-col :span="3"><div class="grid-bottom"></div></el-col>
                  </el-row>

                  <el-row class="form-bottom-row">
                    <el-col :span="3"><div class="grid-bottom"></div></el-col>
                    <el-col :span="18">
                      <div class="grid-bottom grid-border grid-update" style="border:0;">
                        <el-checkbox style="float: left; width: 15px;" v-model="checked">
                          <span style="position: relative; right: 3rem;">记住密码</span>
                        </el-checkbox>
                        <span style="float:right;"  @click="goFogotPw"><a>忘记密码？</a></span>
                      </div>
                    </el-col>
                    <el-col :span="3"><div class="grid-bottom"></div></el-col>
                  </el-row>
                  <el-row class="form-bottom-row">
                    <el-col :span="24">
                      <div class="grid-bottom grid-btn">
                        <el-button class="login-buttom login-btn" :loading="loading"  @click="handleLogin" type="primary">登&nbsp;&nbsp;&nbsp;&nbsp;录</el-button>
                        </div>
                      </el-col>
                  </el-row>
                </div>
                <!-- 登录框登录方式--扫码登录 -->
                <!-- <div class="form-wx-login">
                  <span class="logon-type">其它登录：</span>
                  <el-link class="login-type-img" type="primary" :underline="false">
                    <div class="login-wx-img"></div>
                  </el-link>
                </div> -->
              </div>
            </form>
          </div>
        </el-col>
      </el-row>
    </el-main>

    <!-- 2019-9-16 脚部 -->
    <el-footer id="login-footer" style="height:10rem;">
      <p class="footer-icon">
        <font class="footer-war"><span class="footer-text">合作伙伴</span></font>
      </p>
      <div class="copyright">
        <p v-show="info.icpInfo">技术支持&nbsp;{{info.techSupport||''}}</p>
        <p>
          <span v-show="info.icpInfo">网站ICP备案号：{{info.icpInfo||''}}</span>
          <span v-show="info.icpInfo">Copyright&nbsp;{{info.copyrightInfo||''}}</span>
        </p>
      </div>
    </el-footer>

    <!-- 微信登录的二维码弹框 -->
    <!-- <el-dialog :visible.sync="wxLoginPop" width="25%" >
      <div id="login-code">

      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="wxLoginPop = false">取 消</el-button>
        <el-button type="primary" @click="wxLoginPop = false">确 定</el-button>
      </span>
    </el-dialog> -->
  </el-container>
</template>

<script>
import { getSiteInfo } from '@/modules/login/api/login'
import { returnThumbnailUrl, returnFilesUrl, uploadUrl } from '@/modules/file/api/upload' // 图片操作API
import './api/wxLogin'

export default {
  name: 'login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      siteInfo: {
        bg: '',
        logo: ''
      },
      bgStyle: {
        background: '',
        backgroundSize: ''
      },
      loading: false,
      pwdType: 'password',
      info: {},
      checked: false,
      borderColor: false,
      fontColor: false,
      leftImage: '',
      wx: {
        appid:'wx4ca4a3a2098ba401',
        scope:'',
        redirect_uri: 'http://pay.oem.400fzy.com/notify/receive_ticket',
        code: ''
      }
    }
  },
  created() {
    var _self = this;
    document.onkeydown = function(e){
      var key = window.event.keyCode;
      if(key == 13 || key == 100){
          _self.getInfo();
      }
    }
  },
  mounted () {
    // 获取默认信息
    this.getInfo()
    this.getCookie()
    //微信扫码登录
    //this.codeLogin()
  },
  methods: {
    codeLogin(){
      let accredit = 'https://open.weixin.qq.com/connect/qrconnect?'+
                      'appid='+ this.wx.appid + 
                      '&redirect_uri='+ this.wx.redirect_uri +
                      '&response_type='+ this.wx.code +
                      '&scope=snsapi_login'+
                      '#wechat_redirect'
      let obj = new WxLogin({
        self_redirect: true,
        id: "login-code", 
        appid: this.wx.appid, 
        scope: "snsapi_login", 
        redirect_uri: this.wx.redirect_uri,
        style: "black"
      });
    },
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    handleLogin () {
      // 判断复选框是否被勾选 勾选则调用配置cookie方法
      if (this.checked) {
        // 传入账号名，密码，和保存天数3个参数
        this.setCookie(this.loginForm.username, this.loginForm.password, 7)
      } else {
        // 清空Cookie
        this.clearCookie()
      }
      this.loading = true
      this.$store.dispatch('login', this.loginForm).then(() => {
        this.$router.replace({ path: '/' })
      }).catch(() => {
        this.loading = false
      })
    },
    /**
     * 获取信息
     */
    getInfo: function () {
      getSiteInfo().then(res => {
        console.log(res)
        let data = res.obj
        // 如果有背景图
        if (data.bgmPhoto !== '' && data.bgmPhoto != null) {
          this.leftImage = this.getImg(data.bgmPhoto);
          // this.siteInfo.bg = "url('" + this.getImg(data.bgmPhoto) + "') 50% no-repeat"
          // this.bgStyle.background = this.siteInfo.bg // 背景图片background: url(/static/img/bg.e499aee.png) 50% no-repeat;
          // this.bgStyle.backgroundSize = 'cover' // 100%大小
        }
        // 如果有LOGO
        if (data.logo !== '' && data.logo != null) {
          // this.siteInfo.logo = this.getImg(data.logo)
          sessionStorage.removeItem('logoImg')
          sessionStorage.setItem('logoImg', this.getImg(data.logo))
        }
        // 如果有LOGO
        if (data.loginLogo !== '' && data.loginLogo != null) {
          this.siteInfo.logo = this.getImg(data.loginLogo)
          sessionStorage.removeItem('logoImg')
          sessionStorage.setItem('logoImg', this.getImg(data.loginLogo))
        }
        this.info = data
      })
    },
    /**
     * 换取图片
     */
    getImg: function (val) {
      let reg = /^[0-9]*$/
      if (reg.test(val)) {
        return returnFilesUrl(val)
      } else {
        let base64 = 'data:image/png;base64,'
        return base64
      }
    },
    /**
     * 忘记密码
     */
    goFogotPw () {
      this.$alert('请联系管理员！', '提示', {
        confirmButtonText: '确定',
        type: 'warning',
        callback: action => {
        }
      })
      // this.$router.push({ path: '/fogot' })
    },
    // 设置cookie
    setCookie (c_name, c_pwd, exdays) {
      var exdate = new Date() // 获取时间
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) // 保存的天数
      // 字符串拼接cookie
      window.document.cookie = 'userName' + '=' + c_name + ';path=/;expires=' + exdate.toGMTString()
      window.document.cookie = 'userPwd' + '=' + c_pwd + ';path=/;expires=' + exdate.toGMTString()
    },
    // 读取cookie
    getCookie () {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split('; ') // 这里显示的格式需要切割一下自己可输出看下
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split('=') // 再次切割
          // 判断查找相对应的值
          if (arr2[0] == 'userName') {
            this.loginForm.username = arr2[1] // 保存到保存数据的地方
            this.checked = true
          } else if (arr2[0] == 'userPwd') {
            this.loginForm.password = arr2[1]
          }
        }
      }
    },
    // 清除cookie
    clearCookie: function () {
      this.setCookie('', '', -1) // 修改2值都为空，天数为负1天就好了
    },
    //修改color
    updateColor (item) {
      console.log(item)
      if(item == 'user'){
        this.borderColor = true
        this.fontColor = false
      }else if(item == 'pwd'){
        this.fontColor = true
        this.borderColor = false
      }
    }

  }
}
</script>

<style>
  .copyright {
    font-size: 14px;
    color: #828282;
    text-align: center;
    padding: 0.25rem 0 0 0;
  }
</style>



